import React, { useState } from 'react'

import Button from 'components/Button'

import { ReactComponent as SoundOffIcon } from 'assets/images/soundOffIcon.svg'
import { ReactComponent as SoundOnIcon } from 'assets/images/soundOnIcon.svg'

import './styles.scss'

const audioBgPath = require('assets/sounds/bg-sound.mp3')

const ButtonSound = () => {
  const [audio, setAudio] = useState('')

  const handleClick = () => setAudio(audio ? '' : audioBgPath.default)

  return (
    <Button className="button-sound" onClick={handleClick}>
      {audio ? <SoundOnIcon /> : <SoundOffIcon />}
      <audio src={audio} autoPlay />
    </Button>
  )
}

export default ButtonSound
