import React, { useState, useEffect } from 'react'

import moment from 'moment'

import { UTC_RELEASE_DATE, COUNTER_FORMAT } from 'consts/counter'

const HeaderCounter = () => {
  const [counter, setCounter] = useState(0)

  useEffect(() => {
    setInterval(() => {
      const releaseDate = moment(UTC_RELEASE_DATE)
      const currentDate = moment()
      const timeToRelease = releaseDate.diff(currentDate)

      setCounter(timeToRelease < 0 ? 0 : timeToRelease)
    }, 1000)
  }, [])

  const counterFormat = moment.utc(counter).format(COUNTER_FORMAT)

  return <>{counterFormat}</>
}

export default HeaderCounter
