import React, { useState, useEffect } from 'react'

import { MotionAnimate } from 'react-motion-animate'
import cx from 'classnames'

import Tooltip from 'components/Tooltip'

import { shuffle, shuffleCouple } from 'helpers/index'

import {
  secondDropList,
  secondDropBoyGirlList,
  secondDropAndrogynousList,
} from 'consts/secondDrop'
import { fadeInLeft } from 'consts/animation'
import Arousal from 'tooltips/Arousal'

import './styles.scss'

const SecondDrop = () => {
  const [secondDrop, setSecondDrop] = useState(secondDropList)
  const [secondDropBoyGirl, setSecondDropBoyGirl] = useState(secondDropBoyGirlList)
  const [secondDropAndrogynous, setSecondDropAndrogynous] = useState(secondDropAndrogynousList)

  useEffect(() => {
    setInterval(() => {
      setSecondDrop([...shuffleCouple(secondDrop)])
      // setSecondDropBoyGirl([...shuffle(secondDropBoyGirl)])
      // setSecondDropAndrogynous([...shuffle(secondDropAndrogynous)])
    }, 3000)
  }, [])

  const secondDropSlice = secondDrop.slice(0, 6)

  return (
    <div className="container second-drop">
      <div id="secondDrop" className="link-anchor" />
      <div className="second-drop__inner">
        <div className="content">
          <div className="second-drop__head">
            <MotionAnimate animation="fadeInUp" speed="0.4">
              <h2 className="second-drop__title">2nd free drop | Sex Hero</h2>
            </MotionAnimate>
            <MotionAnimate animation="fadeInUp" delay={0.25} speed="0.4">
              <span className="second-drop__sub-title">
                {/* 18 Nov 2021 / 8 p.m. */}
              </span>
            </MotionAnimate>
            <MotionAnimate animation="fadeInUp" delay={0.5} speed="0.4">
              <p>
                These are the characters that the embryos of the Embry clan turn into. They are not available for purchase by themselves, you can only benefit them when you have Boy sperm, Girl egg and Androgynous embryos on your wallet.
              </p>
              <p className="content__note">
                Sex Hero characters are randomly generated and sent free to their owners. They can see everyone without clothes, control the duration of their partner&apos;s orgasm, and get energy from <Tooltip overlay={Arousal}>arousal*</Tooltip>.
              </p>
            </MotionAnimate>
          </div>
          <div className="second-drop__content">
            <MotionAnimate variant={fadeInLeft} speed="0.4">
              <div className="second-drop__grid">
                {secondDropList.map((item, index) => {
                  const isHide = !secondDropSlice.find(itemImage => itemImage === item)
                  return (
                    <img
                      key={index}
                      src={item}
                      className={cx({ hide: isHide })}
                      alt=""
                    />
                  )
                })}
              </div>
            </MotionAnimate>
            <MotionAnimate animation="fadeInUp" speed="0.4">
              <div className="second-drop__grid-mobile">
                {(new Array(3).fill(null)).map((item, index) => (
                  <img key={index} src={secondDropBoyGirl[index]} alt="" />
                ))}
              </div>
            </MotionAnimate>
            <div className="second-drop__info">
              <MotionAnimate animation="fadeInUp" delay={0.75} speed="0.4">
                <h3>boy/girl hero</h3>
                <p>
                  A Girl Hero and a Boy Hero can be born only if you have a Boy sperm and a Girl egg on the same wallet.
                  And divine randomness decides what exactly Sex Hero character you will get.
                </p>
              </MotionAnimate>
              <div className="second-drop__grid-mobile">
                {(new Array(3).fill(null)).map((item, index) => (
                  <img key={index} src={secondDropAndrogynous[index]} alt="" />
                ))}
              </div>
              <MotionAnimate animation="fadeInUp" delay={1.25} speed="0.4">
                <h3>Androgynous hero</h3>
                <p>
                  An Androgynous Hero comes from the Androgynous embryo.
                </p>
                <div className="second-drop__info-divider" />
                <p className="content__note">
                  Further on, only the owners of &quot;Sex Hero&quot; characters will have access to the game. There they will be able to find a soul mate for their characters and create new types of unique NFTs.
                </p>
              </MotionAnimate>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default SecondDrop
