import React from 'react'

import cx from 'classnames'

import { menuList } from 'consts/menu'

import { ReactComponent as MenuCloseIcon } from 'assets/images/menu-close-icon.svg'
import { ReactComponent as DiscordIcon } from 'assets/images/discord-icon.svg'
import { ReactComponent as TwitterIcon } from 'assets/images/twitter-icon.svg'
import { ReactComponent as TelegramIcon } from 'assets/images/telegram-icon.svg'

import './styles.scss'

interface HeaderMenuPropsTypes {
  open: boolean;
  onClose: (event?: React.MouseEvent<SVGSVGElement>) => void;
}

const HeaderMenu = ({ open, onClose }: HeaderMenuPropsTypes) => {
  const handleLink = (id: string) => {
    const titleElement: HTMLElement | null = document.getElementById(id)
    titleElement && titleElement.scrollIntoView({ behavior: 'smooth' })
    onClose()
  }

  return (
    <div className={cx('header-menu', { 'header-menu--open': open })}>
      <MenuCloseIcon className="header-menu__close-button" onClick={onClose} />
      <div className='header-menu__list'>
        {menuList.map((item, index) => (
          <span key={index} className="header-menu__item" onClick={() => handleLink(item.id)}>
          {item.label}
        </span>
        ))}
        <div className="header-menu__divider" />
        <div className="header-menu__networks">
          <a href="https://discord.gg/Whnt9UzEHN" target="_blank" rel="noreferrer" className="header-menu__networks-item-link">
            <DiscordIcon className="header-menu__networks-item" />
          </a>
          <a href="https://twitter.com/sex_nft" target="_blank" rel="noreferrer" className="header-menu__networks-item-link">
            <TwitterIcon className="header-menu__networks-item" />
          </a>
          {/* <TelegramIcon className="header-menu__networks-item" /> */}
        </div>
      </div>
    </div>
  )
}

export default HeaderMenu
