import React, { useState } from 'react'

import cx from 'classnames'
import { useMediaQuery } from 'react-responsive'
import { MotionAnimate } from 'react-motion-animate'
import { Swiper, SwiperSlide } from 'swiper/react'
import SwiperCore, {
  Pagination
} from 'swiper'

import { roadmapList, roadmapPoints, roadmapPointsMobile } from 'consts/roadmap'
import { fadeInLeft, fadeInDown, fadeIn } from 'consts/animation'

import iconLeft from 'assets/images/roadmap-left.svg'
import iconRight from 'assets/images/roadmap-right.svg'

import RoadmapItem from './components/RoadmapItem'

import './styles.scss'

SwiperCore.use([Pagination])

const Roadmap = () => {
  const [swiper, setSwiper] = useState<SwiperCore>()
  const [activeSlide, setActiveSlide] = useState<number>(0)

  const isMobile = useMediaQuery({
    query: '(max-width: 460px)'
  })

  const handleChangeSlide = (value: number) => {
    swiper?.slideTo(value < 2 ? 0 : value - 2)
    setActiveSlide(value)
  }

  const handleChangeSlideMobile = (value: number) => {
    swiper?.slideTo(value)
    setActiveSlide(value)
  }

  const handlePrev = () => {
    if (prevDisabled) return
    swiper?.slideTo(activeSlide - 1)
    setActiveSlide(activeSlide - 1)
  }
  const handleNext = () => {
    if (nextDisabled) return
    swiper?.slideTo(activeSlide + 1)
    setActiveSlide(activeSlide + 1)
  }

  const progressLineValue = (isMobile ? roadmapPointsMobile : roadmapPoints)[activeSlide]
  const prevDisabled = activeSlide === 0
  const nextDisabled = activeSlide === 6

  const slidesPerView = isMobile ? 1 : 4
  const slidesGap = isMobile ? 0 : 50

  return (
    <MotionAnimate variation={fadeIn} speed="0.4">
      <div className="roadmap">
        <div id="roadmap" className="link-anchor" />
        <div className="container roadmap__inner">
          <div className="content">
            <MotionAnimate animation="fadeInUp" speed="0.4">
              <h2 className="roadmap__title">
                <>ROADMAP</>
                <div className="roadmap__navigation">
                  <div
                    onClick={handlePrev}
                    className={cx('roadmap__navigation-button', { 'roadmap__navigation-button--disabled': prevDisabled })}
                  >
                    <img src={iconLeft} alt="" />
                  </div>
                  <div
                    onClick={handleNext}
                    className={cx('roadmap__navigation-button', { 'roadmap__navigation-button--disabled': nextDisabled })}
                  >
                    <img src={iconRight} alt="" />
                  </div>
                </div>
              </h2>
            </MotionAnimate>
            <Swiper
              className="roadmap__slider"
              onSwiper={setSwiper}
              navigation
              spaceBetween={slidesGap}
              slidesPerView={slidesPerView}
              pagination={{
                clickable: true,
              }}
              centerInsufficientSlides={true}
              simulateTouch={isMobile}
              initialSlide={0}
              onSlideChange={() => handleChangeSlideMobile(swiper?.activeIndex || 0)}
            >
              {roadmapList.map((item, index) => (
                <SwiperSlide key={index} onClick={() => handleChangeSlide(index)}>
                  <RoadmapItem {...item} index={index} activeSlide={activeSlide} progressLineValue={progressLineValue} />
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        </div>
      </div>
    </MotionAnimate>
  )
}

export default Roadmap
