import React from 'react'

import { MotionAnimate } from 'react-motion-animate'
import { Swiper, SwiperSlide } from 'swiper/react'
import SwiperCore, {
  Pagination, Navigation, Autoplay
} from 'swiper'
import cx from 'classnames'

import LabelSay from 'components/LabelSay'

import { fadeInLeft, fadeInRight } from 'consts/animation'

import './styles.scss'

SwiperCore.use([Pagination, Navigation, Autoplay])

interface DropItemPropsTypes {
  title?: string;
  price?: string;
  description?: string;
  note: string;
  reverted?: boolean;
  imageFormula?: string;
  phrase?: string;
  sliderImages?: Array<string>;
  delay?: number;
}

const DropItem = ({
  title, price, description,
  note, reverted, imageFormula,
  phrase, sliderImages = [], delay,
}: DropItemPropsTypes) => {
  return (
    <div className={cx('drop-item', { 'drop-item--reverted': reverted })}>
      <div className="content drop-item__info">
        <MotionAnimate variant={reverted ? fadeInRight : fadeInLeft} delay={0.5} speed="0.4">
          <div className="drop-item__info-top">
            <h2 className="drop-item__info-title">{title}</h2>
            <h4 className="drop-item__info-price">{price}</h4>
          </div>
          <p>{description}</p>
          <p className="content__note" dangerouslySetInnerHTML={{ __html: note }} />
          <img className="drop-item__info-formula" src={imageFormula} alt="" />
        </MotionAnimate>
      </div>
      <div className="drop-item__slider">
        <div className="drop-item__slider-phrase">
          <MotionAnimate animation="fadeInUp" delay={0.5} speed="0.4">
            <LabelSay>{phrase}</LabelSay>
          </MotionAnimate>
        </div>
        <MotionAnimate variant={reverted ? fadeInLeft : fadeInRight} speed="0.4">
          <Swiper
            loop
            navigation
            spaceBetween={50}
            slidesPerView={1}
            autoplay={{ delay: delay }}
            // onSwiper={(swiper) => console.log(swiper)}
          >
            {sliderImages.map((item, index) => (
              <SwiperSlide key={index}>
                <img src={item} alt="" />
              </SwiperSlide>
            ))}
          </Swiper>
        </MotionAnimate>
      </div>
    </div>
  )
}

export default DropItem
