import React from 'react'

import TooltipComponent from 'rc-tooltip'

import './styles.scss'

interface TooltipComponentPropsTypes {
  children?: React.ReactNode;
  overlay?: React.ReactNode;
  placement?: string;
}

const Tooltip = ({ children, overlay, placement = 'topLeft' }: TooltipComponentPropsTypes) => {
  return (
    <TooltipComponent
      overlay={overlay}
      placement={placement}
      mouseEnterDelay={0}
      trigger={['hover']}
      overlayClassName="tooltip"
    >
      <span className="tooltip-wrapper">{children}</span>
    </TooltipComponent>
  )
}

export default Tooltip
