import React, { useEffect, useState } from 'react'

import { MotionAnimate } from 'react-motion-animate'
import cx from 'classnames'

import Tooltip from 'components/Tooltip'

import SenspaceShip from 'tooltips/SenspaceShip'
import { shuffleCouple } from 'helpers/index'
import { fadeInLeft } from 'consts/animation'

import em1 from 'assets/images/embry-clan/1.png'
import em2 from 'assets/images/embry-clan/2.png'
import em3 from 'assets/images/embry-clan/3.png'
import em4 from 'assets/images/embry-clan/4.png'
import em5 from 'assets/images/embry-clan/5.png'
import em6 from 'assets/images/embry-clan/6.png'
import em7 from 'assets/images/embry-clan/7.png'
import em8 from 'assets/images/embry-clan/8.png'
import em9 from 'assets/images/embry-clan/9.png'
import em10 from 'assets/images/embry-clan/10.png'
import em11 from 'assets/images/embry-clan/11.png'
import em12 from 'assets/images/embry-clan/12.png'
import em13 from 'assets/images/embry-clan/13.png'
import em14 from 'assets/images/embry-clan/14.png'
import em15 from 'assets/images/embry-clan/15.png'
import em16 from 'assets/images/embry-clan/16.png'
import em17 from 'assets/images/embry-clan/17.png'
import em18 from 'assets/images/embry-clan/18.png'

import './styles.scss'

const clanImages = [
  em1, em2, em3,
  em4, em5, em6,
  em7, em8, em9,
  em10, em11, em12,
  em13, em14, em15,
  em16, em17, em18,
]

const Clan = () => {
  const [images, setImages] = useState(clanImages)

  useEffect(() => {
    setInterval(() => {
      setImages([...shuffleCouple(images)])
    }, 850)
  }, [])

  const images9 = images.slice(0, 9)

  return (
    <div className="container clan">
      <div className="clan__inner">
        <MotionAnimate animation="fadeInUp" speed="0.4">
          <div className="content">
            <h2 className="clan__title-mobile">EMBRY CLAN</h2>
          </div>
        </MotionAnimate>
        <MotionAnimate variant={fadeInLeft} speed="0.4">
          <div className="clan__grid">
            {clanImages.map((item) => {
              const isHide = !images9.find(itemImage => itemImage === item)
              return (
                <img
                  key={item}
                  src={item}
                  className={cx({ hide: isHide })}
                  alt=""
                />
              )
            })}
          </div>
        </MotionAnimate>
        <div className="content clan__content">
          <MotionAnimate animation="fadeInUp" delay={0.5} speed="0.4">
            <h2 className="clan__content-title">EMBRY CLAN</h2>
          </MotionAnimate>
          <MotionAnimate animation="fadeInUp" delay={0.75} speed="0.4">
            <p>
              The Embry clan is a set of 6,900 unique NFTs of three types:<br />
              Boy sperm, Girl egg and Androgynous. All of them are freedom-loving and have the property of absorbing glances. They love to smoke illegal worldwide <Tooltip placement="topLeft" overlay={SenspaceShip}>Senspace-ship*</Tooltip> and are crazy about sex.
            </p>
          </MotionAnimate>
        </div>
      </div>
    </div>
  )
}

export default Clan
