import React from 'react'

import cx from 'classnames'
import { MotionAnimate } from 'react-motion-animate'
import { useMediaQuery } from 'react-responsive'

import { fadeInLeft, fadeInDown } from 'consts/animation'

import { ReactComponent as RmPoint } from 'assets/images/rm-point.svg'

import './styles.scss'

interface RoadmapItemPropsTypes {
  value: string;
  image: string;
  options: Array<string>;
  index: number;
  progressLineValue: string;
  activeSlide: number;
}

const RoadmapItem = ({ value, image, options, index, progressLineValue, activeSlide }: RoadmapItemPropsTypes) => {
  const isMobile = useMediaQuery({
    query: '(max-width: 460px)'
  })

  const isFirstItem = index === 0
  const isActive = index <= activeSlide

  return (
    <div className="roadmap-item">
      <div className="roadmap-item__point-wrapper">
        <RmPoint className={cx('roadmap-item__point', { 'roadmap-item__point--active': isActive })} />
      </div>
      <MotionAnimate animation="fadeInUp" speed="0.4">
        <img className={cx('roadmap-item__image', { 'roadmap-item__image--disabled': !isActive })} src={image} alt="" />
      </MotionAnimate>
      {isMobile
        ? (
          <div className="content roadmap-item__content">
            <span className="roadmap-item__title">{value}</span>
            <ul className="roadmap-item__list">
              {options.map((item, index) => (
                <li key={index}>
                  <p className="roadmap-item__list-item content__note" dangerouslySetInnerHTML={{ __html: item }} />
                </li>
              ))}
            </ul>
          </div>
        )
        : (
          <MotionAnimate variant={fadeInDown} speed="0.4">
            <div className="content roadmap-item__content">
              <span className="roadmap-item__title">{value}</span>
              <ul className="roadmap-item__list">
                {options.map((item, index) => (
                  <li key={index}>
                    <p className="roadmap-item__list-item content__note" dangerouslySetInnerHTML={{ __html: item }} />
                  </li>
                ))}
              </ul>
            </div>
          </MotionAnimate>
        )}
      {isFirstItem && <div className="roadmap__progress-line" style={{ width: progressLineValue }} />}
    </div>
  )
}

export default RoadmapItem
