export function shuffle(array) {
  let currentIndex = array.length,  randomIndex

  // While there remain elements to shuffle...
  while (currentIndex != 0) {

    // Pick a remaining element...
    randomIndex = Math.floor(Math.random() * currentIndex)
    currentIndex--;

    // And swap it with the current element.
    [array[currentIndex], array[randomIndex]] = [
      array[randomIndex], array[currentIndex]]
  }

  return array
}

export function shuffleCouple(array) {
  const shuffledArray = [...array]
  let currentIndex = array.length
  let randomIndex1, randomIndex2

  randomIndex1 = Math.floor(Math.random() * currentIndex)
  randomIndex2 = Math.floor(Math.random() * currentIndex)

  shuffledArray[randomIndex1] = array[randomIndex2]
  shuffledArray[randomIndex2] = array[randomIndex1]

  return [...shuffledArray]
}
