import React from 'react'

import { Swiper, SwiperSlide } from 'swiper/react'
import SwiperCore, {
  Pagination, Navigation, Autoplay
} from 'swiper'
import { useMediaQuery } from 'react-responsive'
import { LightgalleryItem } from 'react-lightgallery'
import { MotionAnimate } from 'react-motion-animate'

import { heroesList } from 'consts/heroes'
import { fadeInLeft } from 'consts/animation'

import './styles.scss'

SwiperCore.use([Pagination, Navigation, Autoplay])

const HeroesSlider = () => {
  const isMobile = useMediaQuery({
    query: '(max-width: 460px)'
  })

  return (
    <MotionAnimate variant={fadeInLeft} delay={0.5} speed="0.4">
      <div className="heroes-slider">
        <Swiper
          loop
          navigation
          allowTouchMove={isMobile ? true : false}
          spaceBetween={0}
          slidesPerView={isMobile ? 1 : 4}
          centerInsufficientSlides
          autoplay={{ delay: 1500 }}
        >
          {heroesList.map((item, index) => (
            <SwiperSlide key={index}>
              {/* <LightgalleryItem group="any" src={item.image} itemClassName="heroes-slider__item"> */}
              <img src={item.image} alt="" />
              {/* </LightgalleryItem> */}
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </MotionAnimate>
  )
}

export default HeroesSlider
