import React from 'react'

import { MotionAnimate } from 'react-motion-animate'

import { faqList } from 'consts/faq'
import { fadeInLeft } from 'consts/animation'

import logo from 'assets/images/logo.png'
import footerBg from 'assets/images/footer-bg.png'

import FaqItem from './components/FaqItem'

import './styles.scss'

const Faq = () => {
  return (
    <div className="faq">
      <div id="faq" className="link-anchor" />
      <div className='faq__inner'>
        <div className='container'>
          <MotionAnimate variant={fadeInLeft} speed="0.4">
            <h2 className="faq__title">FAQ</h2>
          </MotionAnimate>
          <MotionAnimate animation="fadeInUp" delay={0.5} speed="0.4">
            <div className="content faq__content">
              {faqList.map((item, index) => {
                const isLast = faqList.length === (index + 1)
                return <div key={index}>
                  <FaqItem {...item} />
                  {!isLast && <div className="faq__content-divider" />}
                </div>
              })}
            </div>
          </MotionAnimate>
        </div>
      </div>
      <img src={logo} className="faq__logo" alt="" />
      <img src={footerBg} className="faq__bg" alt="" />
    </div>
  )
}

export default Faq
