import React, { useEffect, useRef } from 'react'

import { CSSTransition } from 'react-transition-group'
import Modal from 'react-modal'

import useOnClickOutside from 'hooks/onClickOutside'

import discordButtonImage from 'assets/images/discord-button.png'
import { ReactComponent as CloseIcon } from 'assets/images/close-icon.svg'

import styles from './styles.module.scss'

interface HeaderPresalePropsTypes {
  openModal: boolean;
  setModal: (event: React.MouseEvent<HTMLElement>) => void;
  onCloseModal: () => void;
}

const customStyles = {
  overlay: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 1000,
  },
}

const HeaderPresale = ({
  openModal,
  setModal,
  onCloseModal,
}: HeaderPresalePropsTypes) => {
  const ref = useRef<any>()

  useOnClickOutside(ref, onCloseModal)

  useEffect(() => {
    return (
      Modal.setAppElement('body')
    )
  }, [])

  return (
    <CSSTransition
      in={openModal}
      timeout={300}
      classNames="dialog"
    >
      <Modal
        closeTimeoutMS={500}
        isOpen={openModal}
        style={customStyles}
        className={styles.modal}
      >
        <div ref={ref} className={styles.modalInner}>
          <div className={styles.modalClose} onClick={setModal}>
            <CloseIcon />
          </div>
          <h2 className={styles.modalTitle}>Join Discord for Whitelist</h2>
          {/* <span className={styles.modalSubTitle}>Presale December 27, 2021 18:00 UTC</span> */}
          <span className={styles.modalSubTitle}>Presale 10 January 2022 18:00 UTC</span>
          <a href="https://discord.gg/Whnt9UzEHN" target="_blank" rel="noreferrer" className={styles.modalButton}>
            <img src={discordButtonImage} alt="" />
          </a>
        </div>
      </Modal>
    </CSSTransition>
  )
}

export default HeaderPresale