import React, { useState, useEffect } from 'react'

import cx from 'classnames'

import Button from 'components/Button'
import ButtonSound from 'components/ButtonSound'

import { ReactComponent as DiscordIcon } from 'assets/images/discord-icon.svg'
import { ReactComponent as TwitterIcon } from 'assets/images/twitter-icon.svg'
import { ReactComponent as TelegramIcon } from 'assets/images/telegram-icon.svg'
import { ReactComponent as MenuIcon } from 'assets/images/menu-icon.svg'
import logoMobile from 'assets/images/logo-short.png'
import logo from 'assets/images/logo.png'

import HeaderMenu from './components/HeaderMenu'
import HeaderPresale from './components/HeaderPresale'
import HeaderCounter from './components/HeaderCounter'

import './styles.scss'

const Header = () => {
  const [menu, setMenu] = useState(false)
  const [headerDark, setHeaderDark] = useState(false)
  const [presaleModal, setPrelaseModal] = useState(false)

  useEffect(() => {
    const handleUpdateHeader = () => {
      window.scrollY > 1
        ? setHeaderDark(true)
        : setHeaderDark(false)
    }

    window.addEventListener('scroll', handleUpdateHeader)

    return () => {
      headerDark && setHeaderDark(false)
      window.removeEventListener('scroll', handleUpdateHeader)
    }
  }, [])

  const handleMenu = () => {
    setMenu(!menu)
  }

  const handleScrollToTop = () => {
    const homeNode = document.getElementById('home')
    homeNode?.scrollIntoView({behavior: 'smooth'})
  }

  const handleSetModal = () => setPrelaseModal(!presaleModal)
  const handleCloseModal = () => setPrelaseModal(false)

  return (
    <>
      <div className="header">
        <div className={cx('header__background', { 'header__background--dark': headerDark })} />
        <div className="container header__inner">
          <img src={logo} className="header__logo" alt="" onClick={handleScrollToTop} />
          <img src={logoMobile} className="header__logo-mobile" alt="" onClick={handleScrollToTop} />
          <div className="header__gap-mobile" />
          <span className="header__counter">
            <HeaderCounter />
          </span>
          <div className="header__gap" />
          <Button onClick={handleSetModal}>PRESALE</Button>
          <div className="header__networks">
            <a href="https://discord.gg/Whnt9UzEHN" target="_blank" rel="noreferrer" className="header__networks-button">
              <DiscordIcon />
            </a>
            <a href="http://twitter.com/sex_nft" target="_blank" rel="noreferrer" className="header__networks-button">
              <TwitterIcon />
            </a>
            {/*<div className="header__networks-button">*/}
            {/*  <TelegramIcon />*/}
            {/*</div>*/}
          </div>
          <div className="header__menu-button">
            <MenuIcon onClick={handleMenu} />
            <HeaderMenu open={menu} onClose={handleMenu}  />
          </div>
        </div>
        <HeaderPresale
          openModal={presaleModal}
          setModal={handleSetModal}
          onCloseModal={handleCloseModal}
        />
      </div>
      <ButtonSound />
    </>
  )
}

export default Header
