import React, { useState } from 'react'

import cx from 'classnames'

import expandIcon from 'assets/images/expand-icon.svg'

import './styles.scss'

interface FaqItemPropsTypes {
  title?: string;
  content?: JSX.Element;
}

const FaqItem = ({ title, content }: FaqItemPropsTypes) => {
  const [open, setOpen] = useState(false)

  const handleOpen = () => setOpen(!open)

  return (
    <div onClick={handleOpen} className="faq-item">
      <img
        className={cx('faq-item__expand', { 'faq-item__expand--expanded': open })}
        src={expandIcon}
        alt=""
      />
      <h3 className="faq-item__title">
        {title}
      </h3>
      <div className={cx('faq-item__content', { 'faq-item__content--expanded': open })}>
        <div className='faq-item__gap' />
        <div>{content}</div>
      </div>
    </div>
  )
}

export default FaqItem
