import React from 'react'

import { MotionAnimate } from 'react-motion-animate'

import './styles.scss'

interface TeamItemPropsTypes {
  avatar?: string;
  title?: string;
  note: string;
}

const TeamItem = ({ avatar, title, note }: TeamItemPropsTypes) => {
  return (
    <div className="team-item">
      <MotionAnimate animation="fadeInUp" speed="0.4">
        <img src={avatar} className="team-item__avatar" alt="" />
      </MotionAnimate>
      <div className="content team-item__content">
        <MotionAnimate animation="fadeInUp" delay={0.5} speed="0.4">
          <h3>{title}</h3>
          <p className="content__note team-item__note" dangerouslySetInnerHTML={{ __html: note }} />
        </MotionAnimate>
      </div>
    </div>
  )
}

export default TeamItem
